import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes} from '@angular/router';
import { FormsModule } from '@angular/forms';
import { AppComponent } from './app.component';
import { AboutComponent } from './about/about.component';
import { ContactsComponent } from './contacts/contacts.component';
import { WorksComponent } from './works/works.component';
import { PlateComponent } from './plate/plate.component';
import { LayoutComponent } from './layout/layout.component';
import { GridFlexComponent } from './grid-flex/grid-flex.component';
import { DaynightComponent } from './daynight/daynight.component';
import { AanimationComponent } from './aanimation/aanimation.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RandomQuoteComponent } from './random-quote/random-quote.component';
import { QuoteserviseService } from './quoteservise.service';
import { HttpClientModule } from '@angular/common/http';


const appRoutes: Routes = [
{path: 'About', component: AboutComponent},
{path: 'Contacts', component: ContactsComponent},
{path: 'Works', component: WorksComponent}, 
{path: 'Works/DayNight', component: DaynightComponent},
{path: 'Works/RandomQuotes', component: RandomQuoteComponent},
{path: 'Works/To-Dolist', component: LayoutComponent},
{path: 'Works/Grid-Flex', component: GridFlexComponent},
{path: '', redirectTo: 'Works', pathMatch: 'full'},
{path: '**', component: WorksComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    AboutComponent,
    ContactsComponent,
    WorksComponent,
    PlateComponent,
    LayoutComponent,
    GridFlexComponent,
    DaynightComponent,
    AanimationComponent,
    RandomQuoteComponent
   
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(appRoutes),
    HttpClientModule
  ],
  providers: [QuoteserviseService],
  bootstrap: [AppComponent]
})
export class AppModule { }



