import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-daynight',
  templateUrl: './daynight.component.html',
  styleUrls: ['./daynight.component.scss']
})
export class DaynightComponent implements OnInit {
time="eclipse2";
daylight="canvas2";
star="star2";
land="land2";
tr=false;

  constructor() { }
changetime(){
	if(this.time == "eclipse"){
	this.time="eclipse2";
	this.daylight="canvas2";
	this.star="star2";
	this.land="land2";
	this.tr=true;
	setTimeout(() => {this.tr=false}, 4500);
	}
	else if(this.time == "eclipse2"){
	this.time="eclipse";
	this.daylight="canvas";
	this.star="star";
	this.land="land";
	this.tr=true;
	setTimeout(() => {this.tr=false}, 4500);
	}
}
  ngOnInit() {
  }

}



