import { Component, OnInit } from '@angular/core';
import { QuoteserviseService } from '../quoteservise.service';
import { Quote } from "../class";

@Component({
  selector: 'app-random-quote',
  templateUrl: './random-quote.component.html',
  styleUrls: ['./random-quote.component.scss']
})
export class RandomQuoteComponent implements OnInit {
	quote = new Quote("", "");
	author:string; 
  quot:string;
  auth="auth";
  quota="quote";
  photo="";
  
  constructor(public Quoteservise:QuoteserviseService) {

   }
   public change(){
     if(this.auth=="auth"){
      this.auth="auth1";
     }
     else if (this.auth=="auth1"){
      this.auth="auth";
     }
   }

   public gotQuote() {
   	this.Quoteservise.getRandomQuote().subscribe((data: Quote) => {
		this.quote= new Quote(data.quote, data.author);

		this.author = this.quote.author;
		this.quot = this.quote.quote;
	});
   }

  ngOnInit() {
  }

}



