import { Component, OnInit } from '@angular/core';
import { trigger,state,style,transition,animate,keyframes } from '@angular/animations';

@Component({
  selector: 'app-aanimation',
  templateUrl: './aanimation.component.html',
  styleUrls: ['./aanimation.component.scss'],
  animations: [
  trigger('trig', [
  	state('small', style({ 
  		transform:'scale(0.2)',
  		})),
  	state('large', style({ 
  		transform:'scale(2)',
  		background:'red'
  		})),
  	transition('small <=> large', animate('300ms ease')),
  	]),
  ]
})
export class AanimationComponent implements OnInit {
state="small";
class="card";
animateMe(){
	if(this.state=="small"){
		this.state="large";
	}
	else{
		this.state="small";
	}
}
  constructor() { }

  ngOnInit() {
  }

}



